.addon-sales-card {
	margin: 10px;
	padding: 5px;
	color: #fff;
	text-shadow:  0 2px 3px rgba(0,0,0,.2);
	background: -webkit-gradient(linear,left top, right top,from(#79589f),to(#5a5d9f));
	background: -webkit-linear-gradient(left,#79589f,#5a5d9f);
	background: -o-linear-gradient(left,#79589f,#5a5d9f);
	background: linear-gradient(to right,#79589f,#5a5d9f);
	-webkit-box-shadow: 0 2px 3px 2px rgba(0,0,0,.2);
	        box-shadow: 0 2px 3px 2px rgba(0,0,0,.2);
	border-radius: 4px;
	width: 300px;
}

.addon-sales-card h1 {
	text-align: center;
	font-size: 16px;
}


